*{box-sizing:border-box}html{color:#34495e;font-family:sans-serif;font-size:1rem;line-height:1.4;font-size:16px}body{margin:0}.app{display:flex;flex-direction:row;width:100%;box-sizing:border-box}.sidebar{width:18rem;height:100vh;display:flex;flex-direction:column;border-right:1px solid rgba(0,0,0,0.07)}.sidebar__header{border-bottom:1px solid rgba(0,0,0,0.07);padding:0.5rem}.sidebar__search-input{color:#34495e;font-family:sans-serif;font-size:1rem;line-height:1.4;width:100%;border:none;padding:0.5rem;border:1px solid transparent;border-radius:0}.sidebar__search-input:focus{box-shadow:0 0 5px #467fcf;border:1px solid #467fcf;outline:none}.sidebar__list{list-style:none;padding:0;margin:0;overflow-y:auto}.sidebar__result a{display:block;padding:0.5rem 1rem;text-decoration:none;color:#34495e}.sidebar__result a:hover{text-decoration:underline}.sidebar__result a.active{background-color:#e8eff9}.content{height:100vh;overflow-y:auto;flex-grow:1;margin-left:1rem}.page{width:50rem}.page a,.page a:visited{color:#007bff;text-decoration:none}.page a:hover{text-decoration:underline}.page a.external,.page a.external:visited{color:#6e54ba}.page a.missing{color:#d32f2f}.page a.current{font-weight:bold}.page li p{margin:0}.page blockquote{border-left:0.25rem solid #dcdcdc;padding-left:0.5rem;margin:0}
